export function TranslateStatusToRussian(status: string) {
  switch (status) {
    case 'Not yet aired':
      return '  ';
    case 'Currently Airing':
      return '';
    case 'Finished Airing':
      return '';
    case 'upcoming':
      return '  ';
    case 'airing':
      return '';
    case 'complete':
      return '';
    default:
      return status;
  }
}
