export function TranslateTypeToRussian(type: string) {
  switch (type.toLowerCase()) {
    case 'tv':
      return '-';
    case 'movie':
      return '';
    case 'ona':
      return '';
    case 'ova':
      return '';
    case 'special':
      return '';
    case 'music':
      return '';
    default:
      return type;
  }
}
